import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'

const useTxMingingReward = (contract, balance): any => {
  const { account } = useWeb3React()
  const [allowance, setAllowance] = useState(null)
  useEffect(() => {
    const fetch = async () => {
      try {
        const res = await contract.getUserReward(0, account)
        setAllowance(res)
      } catch (e) {
        console.error(e)
      }
    }

    if (account) {
      fetch()
    }
  }, [account, contract, balance])

  return allowance
}

export default useTxMingingReward
