import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'Hit Swap',
  description:
    'The most popular AMM on BSC by user count! Earn CAKE through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by PancakeSwap), NFTs, and more, on a platform you can trust.',
  image: 'https://pancakeswap.finance/images/logo.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  switch (path) {
    case '/':
      return {
        title: `${t('Home')} | Hit Swap`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | Hit Swap`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | Hit Swap`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | Hit Swap`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | Hit Swap`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | Hit Swap`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | Hit Swap`,
      }
    case '/ifo':
      return {
        title: `${t('Initial DAO Offering')} | Hit Swap`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | Hit Swap`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | Hit Swap`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | Hit Swap`,
      }
    default:
      return null
  }
}
