import React from 'react'

const MenuLink: React.FC<any> = ({ href, disable, ...otherProps }) => {
  const isHttpLink = href?.startsWith('https') || href?.startsWith('http')

  return (
    <a
      style={{ pointerEvents: disable ? 'none' : 'auto', filter: disable ? 'grayscale(1)' : 'none' }}
      {...otherProps}
      onClick={(e) => {
        if (isHttpLink) window.open(href)
        window.location.href = href || '/'
      }}
    />
  )
}

export default MenuLink
